.butn {
  display: inline-block;
  min-width: 230px;
  border-radius: 50px;
  box-shadow: 0 3px 10px rgba(75, 175, 80, 0.55);
  background-color: #4baf50;
  background-image: linear-gradient(to top, rgba(146, 197, 78, 0) 0%, rgba(146, 197, 78, 0.79) 99%, rgba(146, 197, 78, 0.8) 100%);
  color: #fff;
  font-family: 'Lato', sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 1;
  text-transform: uppercase;
  letter-spacing: 1.4px;
  text-align: center;
  padding: 19px 18px 17px;
  border: 0;
  transition: all 0.3s ease-in-out;
}

.butn:hover {
  color: #fff;
  box-shadow: 0 3px 15px rgba(75, 175, 80, 0.75);
  background-color: #1fa426;
  background-image: linear-gradient(to top, rgba(146, 197, 78, 0) 0%, rgba(146, 197, 78, 0.5) 99%, rgba(146, 197, 78, 0.5) 100%);
}

.butn:focus {
  color: #fff;
}

.butn_wh {
  box-shadow: 0 3px 20px rgba(18, 18, 18, 0.1);
  background: #fff;
  border: 2px solid #fff;
  color: #4baf50;
  padding: 18px 16px 14px;
}

@media (max-width: 991px) {
  .butn_wh {
    min-width: 90%;
  }
}

.butn_wh:focus {
  color: #4baf50;
}

.butn_wh:hover {
  background: transparent;
  color: #fff;
}

.butn_transp {
  color: #fff;
  background: transparent;
  border: 2px solid #fff;
  padding: 18px 16px 14px;
}

@media (max-width: 991px) {
  .butn_transp {
    min-width: 90%;
  }
}

.butn_transp:focus {
  box-shadow: 0 3px 20px rgba(18, 18, 18, 0.1);
  background: #fff;
  color: #4baf50;
}

.butn_transp:hover {
  box-shadow: 0 3px 20px rgba(18, 18, 18, 0.1);
  background: #fff;
  color: #4baf50;
}

#hamburger {
  display: none;
  position: fixed;
  width: 65px;
  height: 65px;
  border-radius: 50%;
  box-shadow: 0 3px 10px rgba(75, 175, 80, 0.55);
  background-color: #4baf50;
  background-image: linear-gradient(to top, rgba(146, 197, 78, 0) 0%, rgba(146, 197, 78, 0.79) 99%, rgba(146, 197, 78, 0.8) 100%);
  bottom: 40px;
  right: 15px;
  z-index: 9999;
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
}

#hamburger .span-wrap {
  position: relative;
  height: 25px;
  width: 32px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#hamburger span {
  display: block;
  position: absolute;
  height: 5px;
  width: 32px;
  background: #fff;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: .25s ease-in-out;
}

@media (max-width: 1199px) {
  #hamburger {
    display: block;
  }
}

#hamburger span:nth-child(1) {
  top: 0;
}

#hamburger span:nth-child(2), #hamburger span:nth-child(3) {
  top: 9px;
}

#hamburger span:nth-child(4) {
  top: 18px;
}

#hamburger.open span:nth-child(1) {
  top: 9px;
  width: 0;
  left: 50%;
}

#hamburger.open span:nth-child(2) {
  transform: rotate(45deg);
}

#hamburger.open span:nth-child(3) {
  transform: rotate(-45deg);
}

#hamburger.open span:nth-child(4) {
  top: 9px;
  width: 0;
  left: 50%;
}

.modal-backdrop {
  background: #4baf50;
}

.modal-backdrop.show {
  opacity: .75;
}

.modal-dialog {
  pointer-events: auto;
}

.modal-dialog_sm {
  width: 100%;
  max-width: 350px;
  margin: 5% auto 10px;
}

@media (max-width: 386px) {
  .modal-dialog_sm .close-x {
    top: 15px;
    right: 15px;
  }
}

.modal-dialog_xl {
  width: 100%;
  max-width: 1033px;
  margin: 5% auto 10px;
}

@media (max-width: 1076px) {
  .modal-dialog_xl .close-x {
    top: 15px;
    right: 15px;
  }
}

.modal-content {
  box-shadow: 0 3px 55px rgba(21, 21, 22, 0.2);
  background-color: #ffffff;
  border: none;
  border-radius: 0;
  overflow: hidden;
  padding: 100px 30px 30px 30px;
  position: relative;
}

.modal-content__img {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all ease-in-out;
  transition-duration: .3s;
  transition-delay: .6s;
}

.modal-content__hiding-content {
  position: relative;
  transform: translateY(350%);
  transition: all ease-in-out;
  transition-duration: .3s;
  transition-delay: .6s;
}

.modal-content__title {
  color: #333;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.2;
  text-align: center;
  margin-bottom: 10px;
}

.modal-content__subtitle {
  color: #666;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
  text-align: center;
  margin-bottom: 17px;
}

.show .modal-content__img {
  top: 30px;
  transform: translate(-50%, 0);
}

.show .modal-content__hiding-content {
  transform: translatey(0);
}

fieldset {
  padding: 20px 0 0 0;
  border: none;
  position: relative;
}

.modal-input-row {
  position: relative;
}

.modal-butn-row {
  padding-top: 30px;
  text-align: center;
}

.modal-butn-row .butn {
  width: 100%;
  max-width: 290px;
}

.modal-input {
  width: 100%;
  border-radius: 25px;
  border: 1px solid #bcbcbc;
  background-color: transparent;
  color: #333;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  padding: 15px 18px;
}

.modal-input:hover {
  border-color: #92c54e;
}

.modal-input:focus {
  border-color: #92c54e;
}

textarea.modal-input {
  max-width: 100%;
  min-width: 100%;
  min-height: 196px;
  max-height: 196px;
}

.resultval {
  color: #4baf50;
  font-size: 50px;
  font-weight: 700;
  line-height: 1;
  text-align: center;
}

.modal-input textarea + label {
  position: absolute;
  top: 38px;
  left: 18px;
  z-index: 9;
  color: #666;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  pointer-events: none;
  transition: all 0.3s ease-in-out;
}

.modal-input textarea:focus + label {
  top: 3px;
  font-size: 12px;
  color: #92c54e;
}

.modal-input + label {
  position: absolute;
  top: 38px;
  left: 18px;
  z-index: 9;
  color: #666;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  pointer-events: none;
  transition: all 0.3s ease-in-out;
}

.modal-input:focus + label {
  top: 3px;
  font-size: 12px;
  color: #92c54e;
}

label.stay {
  top: 3px;
  font-size: 12px;
  color: #92c54e;
}

.close-x {
  top: -15px;
  right: -15px;
  position: absolute;
  cursor: pointer;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  background-color: #ffffff;
  border: none;
  z-index: 9;
}

.close-x:before {
  content: '';
  position: absolute;
  height: 2px;
  width: 22px;
  top: 19px;
  left: 9px;
  background-color: #bbbcc3;
  transition: all 0.3s ease-in-out;
  transform: rotate(45deg);
}

.close-x:after {
  content: '';
  position: absolute;
  height: 2px;
  width: 22px;
  top: 19px;
  left: 9px;
  background-color: #bbbcc3;
  transition: all 0.3s ease-in-out;
  transform: rotate(135deg);
}

.close-x:hover:before {
  background: #92c54e;
  transform: rotate(-135deg);
}

.close-x:hover:after {
  background: #92c54e;
  transform: rotate(315deg);
}

@media (max-width: 700px) {
  .modal-content__table-wrap {
    overflow-x: scroll;
  }
}

.modal-content__table {
  width: 100%;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 35px;
}

@media (max-width: 700px) {
  .modal-content__table {
    width: 640px;
  }
}

.modal-content__table th {
  background-color: #92c64e;
  height: 52px;
  color: #fff;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.2;
  padding: 5px;
  text-align: center;
  border: 1px solid #fff;
}

.modal-content__table td {
  height: 52px;
  color: #666666;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.1;
  border: 1px solid #fff;
  padding: 5px;
}

.modal-content__table td:first-child {
  font-size: 18px;
  font-weight: 700;
}

.modal-content__table tr:nth-child(1n+1) td:nth-child(odd) {
  background-color: #dbefdc;
}

.modal-content__table tr:nth-child(1n+1) td:nth-child(even) {
  background-color: #c9e7ca;
}

.modal-content__table tr:nth-child(2n+1) td:nth-child(odd) {
  background-color: #c9e7ca;
}

.modal-content__table tr:nth-child(2n+1) td:nth-child(even) {
  background-color: #e9f4dc;
}

.modal-content__table-text {
  color: #333333;
  font-size: 20px;
  font-weight: 400;
  line-height: 1.4;
  text-align: center;
  margin-bottom: 20px;
}

.modal-content__table-butn {
  text-align: center;
}

@media (max-width: 555px) {
  .modal-content__table-butn .butn {
    display: block;
    margin-left: auto !important;
    margin-right: auto !important;
    max-width: 260px;
  }
}

.modal-content__table-butn .butn + .butn {
  margin-left: 30px;
}

@media (max-width: 555px) {
  .modal-content__table-butn .butn + .butn {
    margin-top: 30px;
  }
}

body.modal-open header {
  margin-right: 17px;
}

@media (max-width: 1199px) {
  body.modal-open header {
    margin-right: 0;
  }
}

.modal-content__testemonial-name {
  color: #333333;
  font-size: 18px;
  font-weight: 700;
  line-height: 1;
  text-transform: uppercase;
  letter-spacing: 1.8px;
  margin-bottom: 15px;
  padding-right: 30px;
}

.modal-content__testemonial-date {
  color: #999;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  margin-bottom: 25px;
}

.modal-content__testemonial-text {
  color: #666666;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;
}

.modal-content_testemonial {
  padding-top: 30px;
}

.areaForLoader {
  background-color: #1fa426;
  background-image: linear-gradient(to top, rgba(146, 197, 78, 0) 0%, rgba(146, 197, 78, 0.5) 99%, rgba(146, 197, 78, 0.5) 100%);
  overflow: hidden;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;
}

.preloader {
  position: absolute;
  top: 40%;
  left: 50%;
  margin-left: -100px;
  animation-name: spinner;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-duration: 2s;
  transform-style: preserve-3d;
}

.preloader img {
  height: 100px;
  width: 200px;
}

@-webkit-keyframes spinner {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(-360deg);
  }
}

@keyframes spinner {
  from {
    transform: rotateY(0deg);
  }
  to {
    transform: rotateY(-360deg);
  }
}

