footer {
	padding: 70px 0;
	background-color: #92c64e;
	background-image: linear-gradient(43deg, rgba(213, 228, 32, 0) 24%, rgba(213, 228, 32, 0.5) 100%);
	@media (max-width: 767px) {
		padding: 60px 0 0;
	}
}

.footer-logo-wrap {
	text-align: center;
	margin-bottom: 60px;
	.logo {
		display: inline-block;
	}
}

.footer-links {
	text-align: justify;
	&:after {
		content: "";
		width: 100%;
		height: 0;
		visibility: hidden;
		overflow: hidden;
		display: inline-block;
	}
}

.footer-links__item {
	display: inline-block;
	vertical-align: top;
	@media (max-width: 767px) {
		display: block;
		text-align: center;
		margin-bottom: 35px;
	}
}

.footer-link {
	color: #fff;
	a {
		color: #fff;
		transition: all 0.2s ease-in-out;
		&:hover {
			opacity: .5;
		}
	}
}

.footer-link__title {
	font-size: 18px;
	font-weight: 700;
	line-height: 1;
	margin-bottom: 20px;
	@media (max-width: 767px) {
		margin-bottom: 10px;
	}
}

.footer-location {
	font-size: 14px;
	font-weight: 400;
	line-height: 1.3;
}

.footer-phone {
	color: #fff;
	font-size: 20px;
	font-weight: 400;
	line-height: 1;
}

